/* src/App.css */
.bingo-card {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  max-width: 500px;
  margin: 20px auto;
}

.bingo-row {
  display: contents;
}

.bingo-cell {
  border: 1px solid #ccc;
  padding: 20px;
  text-align: center;
  cursor: pointer;
  user-select: none;
  background-color: #f9f9f9;
  transition: background-color 0.2s;
}

.bingo-cell.marked {
  background-color: lightgreen;
}
